/**
 * @WordPress dependencies
 */
import { __, _x } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { useBlockProps } from '@wordpress/block-editor';
import { Icon } from '@wordpress/components';
// import { createInterpolateElement } from '@wordpress/element';
import { external } from '@wordpress/icons';

/**
 * @Internal dependencies
 */
// import metadata from './block.json';
import arkheIcon from '@src/gutenberg/arkhe-icon';
import { blockIcon as sliderIcon } from '@blocks/slider/icon';
import postListIcon from '@blocks/post-list/icon';
import pageListIcon from '@blocks/page-list/icon';
import restrictedAreaIcon from '@blocks/restricted-area/icon';
import boxLinkIcon from '@blocks/box-links/icon';
// import columnsIcon from '@blocks/columns/icon';
// import { blockIcon as sliderIcon } from '@blocks/slider/icon';

const rssIcon = (
	<svg viewBox='0 0 24 24'>
		<path d='M5 10.2h-.8v1.5H5c1.9 0 3.8.8 5.1 2.1 1.4 1.4 2.1 3.2 2.1 5.1v.8h1.5V19c0-2.3-.9-4.5-2.6-6.2-1.6-1.6-3.8-2.6-6.1-2.6zm10.4-1.6C12.6 5.8 8.9 4.2 5 4.2h-.8v1.5H5c3.5 0 6.9 1.4 9.4 3.9s3.9 5.8 3.9 9.4v.8h1.5V19c0-3.9-1.6-7.6-4.4-10.4zM4 20h3v-3H4v3z'></path>
	</svg>
);

const proBlocks = [
	{ name: 'box-links', title: __('Box links', 'arkhe-blocks'), icon: boxLinkIcon },
	{ name: 'slider', title: __('Slider', 'arkhe-blocks'), icon: sliderIcon },
	{ name: 'post-list', title: __('Post list', 'arkhe-blocks'), icon: postListIcon },
	{ name: 'page-list', title: __('Page list', 'arkhe-blocks'), icon: pageListIcon },
	{ name: 'rss', title: __('RSS', 'arkhe-blocks'), icon: rssIcon },
	{
		name: 'restricted-area',
		title: _x('Restricted Area', 'blcok-name', 'arkhe-blocks'),
		icon: restrictedAreaIcon,
	},
];

/* eslint @wordpress/i18n-translator-comments: 0 */
/* eslint jsx-a11y/anchor-has-content: 0 */

// const getProText = createInterpolateElement(
// 	sprintf(___('Get %s', 'arkhe-blocks'), '<a>Get Arkhe Blocks Pro</a>'),
// 	{
// 		a: (
// 			<a
// 				href='https://arkhe-theme.com/ja/product/arkhe-blocks-pro/'
// 				target='_blank'
// 				rel='noopener noreferrer'
// 			/>
// 		),
// 	}
// );

proBlocks.forEach((proBlock) => {
	registerBlockType(`arkhe-blocks/${proBlock.name}`, {
		apiVersion: 2,
		title: proBlock.title,
		icon: proBlock.icon,
		category: 'arkhe-blocks',
		attributes: {},
		edit: () => {
			const blockProps = useBlockProps({
				className: `arkb-is-pro-block`,
			});
			return (
				<div {...blockProps}>
					<Icon icon={arkheIcon} className='__arkhe_icon' />
					<div className='__title'>
						{__('This block is only available in the PRO version.', 'arkhe-blocks')}
					</div>
					<div className='__btnWrap'>
						<a
							className='__btn'
							href='https://arkhe-theme.com/ja/product/arkhe-blocks-pro/'
							target='_blank'
							rel='noopener noreferrer'
						>
							{__('Get Arkhe Blocks Pro', 'arkhe-blocks')}
							<Icon icon={external} size='1em' />
						</a>
					</div>
				</div>
			);
		},
		save: () => {
			return null;
		},
	});
});
